import "./App.css";
import Tarjeta from "./components/Tarjeta.jsx";
import ListaPokemones from "./components/ListaPokemones2.jsx";
import { PokemonService } from "./components/pokemonService.js";
import React, { useState, useEffect } from "react";

const pokemonService = new PokemonService();

function App() {
  const [data, setData] = useState();
  const [prev, setPrev] = useState();
  const [next, setNext] = useState();
  const [PokemonSeleccionado, setPokemonSeleccionado] = useState();

  useEffect(() => {
    pokemonService.traerPagina().then(function (response) {
      setData(response.results);
      setNext(response.next);
      setPrev(response.previous);
    });
  }, []);

  function datosPokemon(url) {
    return pokemonService.traerPokemon(url);
  }


  if (!data) return <h1 className="loading">loading...</h1>;

  return (
    <main className="App">
      <img src="https://lh3.googleusercontent.com/proxy/uQ85cPaqaXcsuSfYj86WpDiAOPXa2_494gzUQQfAyUwOvSghL-xMsdhFY499A6NvuGEF93SLZ3bymZynMaPxJ1EHEYO_qkOseLhW6pAi61XALjdKVE_XQna5E_Z6WprhyQ" id="pokebola" alt="public\assets\pokeball.png" className="pokebola"/>
      <h1 className="titulo">POKEDEX</h1>

      <Tarjeta pokemon={PokemonSeleccionado} />

      <ListaPokemones
        pokemones={data.map(pok => (pok))}

        siguiente={next}
        anterior={prev}
        onPokemonClick={(pokemon) => {
          datosPokemon(`https://pokeapi.co/api/v2/pokemon/${pokemon.name}`).then(
            (obj) => {
              setPokemonSeleccionado(obj);
            }
          );
        }}

        onAnteriorClick={(A) => {
          pokemonService.traerPagina(A).then(function (response) {
            setData(response.results);
            setPrev(response.previous);
            setNext(response.next);
          });
        }}

        onSiguienteClick={(A) => {
          pokemonService.traerPagina(A).then(function (response) {
            setData(response.results);
            setPrev(response.previous);
            setNext(response.next);
          });
        }}
      />
    </main>
  );
}

export default App;
